:root {
  /* colors*/
  --primary: #9C27B0;
  --primary--dark: #7B1FA2;
  --primary-light: #E1BEE7;
  --secondary-light: #ECECEC;
  --accent: #CDDC39;
  --divider: #BDBDBD;
  --focus: #4f9fcf;
  --error: #dc3545;
  --error-light: #f8d7da;

  /* font colors */
  --primary-text: #212121;
  --secondary-text: #353535;
  --icons-text: #FFFFFF;
  --forms-text: #495057;

  /* fonts */
  --regular-font-size: 1.6rem;

  /* sizes */
  --card-width: 300px;
  --card-height: 300px;
  --card-margin: 0 50px 20px 0;

  /* shadows */
  --std-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  --strong-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--primary);
  color: Var(--icons-text);
  z-index: 10;
}

.header-content {
  margin: 20px 100px;
  font-size: 2.5rem;
}

.header-icon {
  margin-right: 10px;
}


.timer {
  width: var(--card-width);
  height: var(--card-height);
  border: 1px solid var(--primary);
  border-radius: 4px;
  padding: 20px;
  margin: var(--card-margin);
  cursor: pointer;
  background-color: white;
  position: relative;
}

.timer:hover {
  box-shadow: var(--std-shadow);
}

.timer.running {
  background-color: var(--primary);
  color: var(--icons-text);
}

.timer.running:hover {
  box-shadow: var(--strong-shadow);
}

.timer .upper {
  font-size: 6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60%;
  width: 100%;
  color: var(--secondary-light);
}

.timer .lower {
  text-align: center;
  width: 100%;
}

.timer .title {
  font-size: 2rem;
  margin-bottom: 10px;
}

.timer .more {
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 50%;
  padding: 11px 12px;
  color: var(--icons-text);
  border: 1px solid var(--accent);
  background-color: var(--accent);
  cursor: pointer;
}

.timer .more:hover {
  box-shadow: var(--std-shadow);
}


.timer .more-icon {
  font-size: 1.5rem;
}

.edit fieldset {
  border: none;
}

.edit {
  border: 1px solid var(--accent);
  border-radius: 4px;
  padding: 15px 15px 0px 15px;
  width: var(--card-width);
  height: var(--card-height);
  margin: var(--card-margin);
  background-color: white;
}

.edit:hover {
  box-shadow: var(--std-shadow);
}

.edit .row {
  margin-bottom: 10px;
}

.edit label {
  display: block;
  margin-bottom: 5px;
  padding-left: 5px;
}

.edit input {
  width: 100%;
  padding: 5px;
  border-radius: 4px;
  border: 1px solid var(--divider);
  font-size: var(--regular-font-size);
  color: var(--forms-text);
}

.edit input:focus {
  outline: none;
  border: 1px solid var(--focus);
}

.timer .empty {
  color: var(--secondary-text);
  opacity: .4;
}

.edit .button-row {
  display: flex;
  justify-content: space-around;
}

.edit .save-btn,
.edit .cancel-btn,
.edit .reset-btn,
.edit .delete-btn {
  font-size: var(--regular-font-size);
  padding: 1rem 2rem;
  text-transform: uppercase;
  background-color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.edit .save-btn {
  color: var(--primary);
}

.edit .save-btn:hover {
  background-color: var(--primary-light);
}

.edit .cancel-btn {
  color: var(--secondary-text);
}

.edit .cancel-btn:hover {
  background-color: var(--secondary-light);
}

.edit .divider {
  border: 0.5px solid var(--accent);
}

.edit .reset-btn {
  color: var(--secondary-text);
  display: block;
  margin-bottom: 2px;
}

.edit .reset-btn:hover {
  background-color: var(--secondary-light);
}

.edit .delete-btn {
  color: var(--error);
}

.edit .delete-btn:hover {
  background-color: var(--error-light);
}

.edit .delete-btn,
.edit .reset-btn {
  width: 100%;
}

.edit .cancel-btn:focus,
.edit .reset-btn:focus {
  box-shadow: 0 0 0.2rem rgba(108,117,125,.5);
}

.edit .save-btn:focus {
  box-shadow: 0 0 0.2rem rgba(122, 53, 125, 0.5);
}

.edit .delete-btn:focus {
  box-shadow: 0 0 0.2rem rgba(125, 0, 19, 0.5);
}

.create-icon {
  font-size: 5rem;
}

.create-button {
  color: var(--primary-light);
  border: 1px solid var(--primary);
  border-radius: 4px;
  background-color: white;
  width: var(--card-width);
  height: var(--card-height);
  margin: var(--card-margin);
  cursor: pointer;
}

.create-button:hover {
  box-shadow: var(--std-shadow);
  color: var(--primary);
}
.list {
  margin: 150px 50px 100px 100px;
  display: flex;
  flex-wrap: wrap;
}
html {
  box-sizing: border-box;
  font-size: 10px;
}

*, *:before, *:after {
  box-sizing: inherit;
}

